.page-body {
  font-family: "Open Sans", Arial, sans-serif;
  margin: 0;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr auto;
  grid-template-areas:
    "header"
    "main"
    "footer";
  padding-top: 80px;

  @media (min-width: @tablet-width) {
    padding-top: 0;
  }
}

.page-body__header {
  background-color: var(--white);
  grid-area: header;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 4;

  @media (max-width: 767px) {
    &--show {
      height: 80px;
      overflow: hidden;
    }
  }
}

.page-body__main {
  grid-area: main;
}

.page-body__footer {
  grid-area: footer;
}
